*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "jameel Noori Nastaleeq";
}
.main-div
{
  /* display: grid;
  grid-template-columns: 3,1fr; */
  justify-content: space-around;
  flex-wrap: wrap;
  display: flex;
  width: 1170px;
  margin: auto;
}
.treaning__itam
{
  border-radius: 18px;
  background-color: #40739e;
  color: #fff;
  box-shadow: 0px 0px 12px 5px #273c75;
  flex-basis: 47%;
  margin: 10px 0px;
  padding: 50px;
  text-align: center;
  padding: 22px 0px;
}
.bgColorTextCenter
{
  background-color: #273c75;
  width: 100%;
  height: 100px;
  text-align: center;
  padding-top: 20px;
  text-transform: uppercase;
  color: azure;
  border-radius: 10px;
  
}
.btns
{
  display: flex;
  justify-content: space-around;
  color: azure;
}
.btns button
{
  background: #273c75;
  padding: 10px 30px;
  color: #efefef;
  border: none;
  border-radius: 10px;
  margin: 10px 0px;
  font-size: 25px;
}
.cont
{
  width: 150px;
  height: 150px;
  background-color: aquamarine;
  margin:10px 35%;
  padding-bottom:40px ;
  border-radius: 150px;
  font-size: 80px;
}
.ncardname
{
  background-color: #273c75;
  margin: 20px 30px;
  padding: 30px;
  font-size: 60px;
  color: #fff;
  direction: rtl;
  border-radius: 20px;
}
.treaning__itam h2
{
  padding-bottom: 20px;
  font-size: 50px;
  direction: rtl;
}
.treaning__itam p
{
  padding:0 30px;
  font-size: 30px;
  direction: rtl;
}
@media (max-width: 740px){
  .main-div
  {
    width: 100%;
    flex-direction: column;
  }
  .treaning__itam
  {
    border-radius: 20px;
    margin: 10px;
  }
}